:root{
  --bege: #fff1ec;
  --laranja: #EA994F;
  --queimado: #ffe4da;
  --lilas: #9B84C4;
  --preto: #222021;
}

body {
  background-color: var(--bege);
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}


/* Fonte do Placeholder */
input::placeholder,
textarea::placeholder{
  font-family: "Montserrat", sans-serif;
  color: var(--lilas);
}

/* Retira o "Azulzinho" em volta dos inputs e textarea */
input:focus,
textarea:focus{
  outline: none;
}

a,
nav button {
  text-decoration: none;
  background-color: transparent;
  border: none;
  color: var(--preto);
  transition: .4s;
  font-size: 1em;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
}

a:hover,
nav button:hover{
  color: var(--lilas);
}

/* Form Styles */

form{
  max-width: 40%;
  margin: 0 auto;
}

label{
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

label span{
  margin-bottom: .3em;
  font-weight: bold;
  text-align: left;
}

input,
textarea {
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid;
  padding: .8em 0;
  background-color: transparent;
}

.btn{
  background-color: var(--preto);
  color: var(--laranja);
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  width: 120px;
  font-weight: bold;
  border: none;
  padding: 10px 15px;
  font-size: 1em;

  transition: .5s;
}

.btn:hover{
  background-color: #3d4d34;
}

.btn-dark{
  border-radius: 0;
  background-color: var(--laranja);
  color: var(--preto);
  border: 1px solid var(--preto);
}

.btn-link{
  border-radius: 0;
  background-color: transparent;
  color: var(--preto);
  position: relative;

  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  width: 120px;
  font-weight: bold;
  border: none;
  padding: 10px 15px;
  font-size: 1em;

  transition: .5s;
}

.btn-link::after{
  content: "";
  background-color: var(--preto);
  width: 35px;
  height: 1px;
  position: absolute;
  left: calc(50% - 17px);
  bottom: 0;

  transition: .5s;
}

.btn-link:hover{
  transform: scale(102%);
}

.btn-link:hover::after{
  width: 45px;
  left: calc(50% - 22.5px);
}

.btn-outline{
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 10px;
  color: var(--laranja);
  
}

.btn-outline:hover{
  transform: scale(110%);
}

.btn-edit:hover{
  color: green;
}

.btn-view:hover{
  color: blue;
}

.btn-delete{
  border: none;
  transition: .5s;
}

.btn-delete:hover{
  color: red;
  cursor: pointer;
  
}

button[disabled]{
  background-color: #aaa;
}

.container .error {
  color: #721c24;
  background-color: #f8d7da;
  border: 1px sokid #f5c6cb;
  padding: 5px;
  border-radius: 5px;
}

h1{
  text-align: center;
}